@import url(https://fonts.googleapis.com/css?family=Roboto:500);
.App {
  text-align: center;
  padding: 10px;
}

.App * {
  box-sizing: border-box;
}

h1 {
  font-size: 1.5em;
}
h1.app_title {
  font-size: 2em;
  color: #f1f1f1;
}
h2, h4 {
  color: #f1f1f1;
}
a {
  color: #009688;
}

ul {
  padding: 0;
  list-style: none;
}

p {
  margin: 5px 0;
}

label {
  color: #f1f1f1;
  padding: 10px;
}

.offline {
  color: #f1f1f1;
  padding: 10px;
  margin-top: 20px;
  border: 2px solid #ff9800;
  display: inline-block;
}

.display-none {
  display: none;
}
.showSuggestions {
  display: block;
}

input[type="text"],
input[type="number"] {
  padding: 8px 6px 8px 10px;
  border-radius: 4px;
  outline: none;
  border: 1px solid #ccc;
  font-size: 14px;
  line-height: 20px;
  align-self: center;
  height: 38px;
}

.city {
  width: 250px;
}

.CitySearch, .NumberOfEvents {
  display: flex;
  flex-direction: column;
  flex-basis: 300px;
  max-width: 300px;
  margin: auto;
  position: relative;
}

.citySearchContainer{
  text-align: center;
}

.numberInput {
  text-align: center;
}

.suggestions {
  width: 250px;
  position: absolute;
  left: 25px;
  z-index: 2;
  align-self: center;
  margin: 0;
  font-size: 14px;
  box-shadow: 10px 10px 16px -12px rgba(0, 0, 0, 0.75);
}

.suggestions > li {
  text-align: left;
  padding: 8px 6px 8px 10px;
  background-color: #eee;
  cursor: default;
}

.suggestions > li:last-child {
  border-bottom-left-radius: 4px;
  border-bottom-right-radius: 4px;
}

.suggestions > li:hover {
  background-color: #bcdcdb;
}

.event {
  padding: 10px 10px 10px;
  margin-bottom: 10px;
  text-align: left;
  border: 1px solid #ccc;
  border-radius: 4px;
  position: relative;
  background-color: #f1f1f1;
  box-shadow: 3px 3px 8px rgba(0, 0, 0, 0.3);
}
.EventList {
  display: block;
  margin-left: auto;
  margin-right: auto;
  max-width: 600px;
}

.details-btn {
  position: static;
  padding: 5px 8px;
  margin: 5px; 
  border-radius: 4px;
  outline: none;
  cursor: pointer;
  background-color: #009688;
  color: #f1f1f1;
}

.name {
  font-size: 18px;
  font-weight: 600;
}

.group-name {
  text-transform: uppercase;
  font-size: 14px;
}

.going,
.visibility {
  font-style: italic;
}

.description {
  overflow-x: hidden;
}

.numberOfEvents {
  width: 200px;
  margin: 20px auto;
  text-align: center;
}

.button-wrapper {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-gap: 20px;
  padding: 20px;
  font-family: sans-serif;
}

.Alert {
  position: static;
  top: 50px;
  color: #ff9800;
}

.recharts-wrapper {
  background-color: #f1f1f1;
  border-radius: 4px;
  box-shadow: 3px 3px 8px rgba(0, 0, 0, 0.3);
}
.data-vis-wrapper {
  display: flex;
  flex-direction: column;
  /* align-items: center; /* vertical positioning. Default is "stretch" */
  padding-top: 5px;
  padding-bottom: 5px;
}
.data-vis-wrapper div {
  /* display: inline-flex; */
  min-width: 50%;
  padding: 10px; 
  overflow: visible !important;
}
.data-vis-wrapper h4 {
  margin: 0px !important;
}
